<template>
	<div>
		<div class="rating-items">
			<span v-for='i in redstar' style="color: red" class="iconfont icon-xingzhuang60kaobei2"></span><span v-for='i in graystar' class="iconfont icon-xingzhuang60kaobei2"></span>
		</div>
	</div>
</template>

<script type="text/javascript">
	export default{
		props:{
			starnum:{
				type:Number
			}
		},
		computed:{
			redstar(){
				return Math.round(this.starnum)
			},
			graystar(){
				return 5-this.redstar
			}
		}
	}
</script>

<style type="text/css" lang='less'>
	.rating-count{
		/*color: red*/
	}
</style>


